.cta{font-family:"Inter",sans-serif;padding:1rem 2rem;font-weight:600;text-transform:uppercase;border-radius:8px;text-decoration:none;transition:all .2s ease;display:inline-block;text-align:center;cursor:pointer}.cta:focus{outline:2px solid hsla(0,0%,100%,.6);outline-offset:2px}.cta:active{transform:translateY(1px);box-shadow:none}.solid{background:#f0c332;color:#000;border:none}.solid:hover{transform:translateY(-2px)}.outlined{background:rgba(0,0,0,0);color:#fff;border:2px solid #fff}.outlined:hover{background:hsla(0,0%,100%,.1);transform:translateY(-2px)}.disabled,:disabled{opacity:.5;cursor:not-allowed;pointer-events:none}